
nav {
    float: right;
    font-size: 16px;
    padding-right: 50px;
	}

nav ul {
    list-style: none;
    margin: 0;
    padding: 0
}

nav ul li {
    float: left;
    position: relative;
	z-index: 25;
	
}

nav ul li a {
    display: block;
    padding: 0 20px;
    line-height: 50px;
    background: #262626;
    color: #fff;
    text-decoration: none
}

nav ul li a:hover {
    background: #F1C232; 
    color: #000;
}

nav ul li a:not(:only-child):after {
    padding-left: 4px;
    content: ' â–¾'
}

nav ul li ul li {
    min-width: 190px
}

nav ul li ul li a {
    padding: 15px;
    line-height: 20px;
    z-index: 25;
}

.nav-dropdown {
    position: absolute;
    display: none;
    font-size: 14px;
	z-index: 25;
	box-shadow: 0 3px 12px rgba(0, 0, 0, 0.15)
}

.nav-mobile {
    display: none;
    position: absolute;
    top: 0;
    right: 5px;
    background: #262626;
    height: 50px;
    width: 50px;
	z-index: 25;
	
}

#nav-toggle {
    position: absolute;
    top: 2px;
    cursor: pointer;
    padding: 20px 35px 15px 0; // 35px needed for complete hamburger click coverage 
}

#nav-toggle span,
#nav-toggle span:before,
#nav-toggle span:after {
    cursor: pointer;
    border-radius: 1px;
    height: 5px;
    width: 35px;
    background: #fff;
    position: absolute;
    display: block;
    content: '';
    transition: all 300ms ease-in-out
}

#nav-toggle span:before {
    top: -10px
}

#nav-toggle span:after {
    bottom: -10px
}

#nav-toggle.active span {
    background-color: transparent
}

#nav-toggle.active span:before,
#nav-toggle.active span:after {
    top: 0
}

#nav-toggle.active span:before {
    transform: rotate(45deg)
}

#nav-toggle.active span:after {
    transform: rotate(-45deg)
}

@media only screen and (max-width: 800px) {
    .nav-mobile {
        display: block;
		z-index: 25;
    }
    nav {
        width: 100%;
        padding: 50px 0 0
    }
    nav ul {
        display: none
    }
    nav ul li {
        float: none
    }
    nav ul li a {
        padding: 15px;
        line-height: 20px
    }
    nav ul li ul li a {
        padding-left: 30px
    }
    .nav-dropdown {
        position: static
    }
}

@media screen and (min-width: 800px) {
    .nav-list {
        display: block !important;
            z-index: 25;
        
    }
}




// my addition limits nav items slide to the right
@media screen and (min-width: 1000px) {
    .nav-list {
         position: absolute;
         left: 600px;
    
    }
}

.navigation {
    height: 50px;
    width: auto;
    background: #262626;
	grid-row:  1;
	grid-column: 1/8;
}

.nav-container {
  margin: 0 auto
}

.brand {
    position: absolute;
    padding-left: 18px;
    float: left;
    line-height: 50px;
    text-transform: uppercase;
    font-size: 1.4em
}

.brand a:link
{
color: #F1C232;    
text-decoration: none
}

.brand a:visited {
    color: #F1C232;
    text-decoration: none
}

.brand a:hover {
    color: #fff;
    text-decoration: none
}

.brand a:active {
    color: #fff;
    text-decoration: none
}


