
.flex_row {
display: flex;	
flex-wrap: wrap;
flex-direction: row;
align-items: flex-start;	
}

.flex_column {
margin-top: 10px;	
}

.parent {
max-width: 1000px;	
display: flex;	
justify-content: flex-start;
flex-wrap: wrap;
flex-direction: row;
align-items: start;
margin-top: 10px;
margin-bottom: 25px;
margin-left: 0;
float: left;
}

.child {
	width: 300px;	
	margin-bottom: 20px;
	margin-left: 20px;
	}

.flex_holder {
display: flex;	
flex-wrap: wrap; /* Allow multiple lines */
}
	
.flex_item {
flex: 1 300px; 
padding: 10px;
min-width: 300px;
margin-right: 10px;
}
