
.footer_row {
grid-row: 4;
grid-column: 1 / 11;
width: 100%;	
display: flex;	
justify-content: space-evenly;
flex-wrap: wrap;
flex-direction: row;
align-items: flex-start;
background-color: #2c2d33;
background-image: linear-gradient(#4C4C4C, #3B3C45);
}

.footer_column {
    width: 225px;
    margin-bottom: 5px;
    padding-bottom: 20px;
    border: 0 solid white
}

.footer_column a { transition: none; }
.footer_column a:link { color: #F1C232; text-decoration: none; }
.footer_column a:visited { color: #9C9C9C; text-decoration: none; }
.footer_column a:hover {color: #fff; text-decoration: underline; }
.footer_column a:active { color: #fff; }


.footer_column li {
  font-size: 14px;
  font-weight: 400;
  margin-top: 5px;
  margin-left: 0;
  list-style-type: none;
}


ul .footer_label {
font-size: 20px;
color: white;	
}



.footer_column img:hover {
background-color: #3B5998;
border-radius: 4px 4px 4px 4px;
}