
img {
  max-width: 100%;
  height: auto;
}

.rounded_corners {
border-radius: 10px;
}

figure {
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 10px;
    margin-left: 0;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-right: 5px;
    padding-left: 5px;
    display: flex;
    flex-flow: column wrap;
    justify-content: flex-start;
    float: left;
}

figcaption {
    color: #222;
    margin: 5px;
    font-size: 14px;
}


.figure_standard {
    max-width: 350px;
    float: left; 
}


.figure_large {
max-width: 600px;
max-height: 450px;
}

