
@import '_nav.scss';
@import '_fonts.scss';
@import '_img.scss';
@import '_footer.scss';
@import '_sig.scss';
@import '_blog.scss';
@import '_button.scss';
@import '_flex.scss';
@import '_card.scss';
@import '_roboto.scss';



body {
min-height: 100vh;
display: grid;
grid-template-columns: 2% 18% 20% 20% 20% 18% 2%;
grid-template-rows: 50px 1fr 50px auto;
font-family: 'Roboto', sans-serif;
font-size: 18px;
font-weight: 400;
line-height: 26px;
margin: 0;
color: #3E454C;
letter-spacing: .75px;
background-image: url("../media/jpg/noisy_grid.jpg");
background-repeat: repeat;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    font-weight: 400;
}

.h1, h1 {font-size: 32px;}
.h2, h2 {font-size: 24px;}
.h3, h3 {font-size: 18px;}

.float_left  {float: left;}
.float_right {float: right;}
.clear_both  {clear: both;}
.clear_right {clear: right;}
.clear_left  {clear: left;}


