 @media screen and (max-width: 800px) {
    .paragraph { 
    float: left;     
    }
}


.email_link {
	display: inline;
    font-size: 30px;
}

.underline {
  text-decoration: none;
  border-bottom: 3px solid #6495ed;
}

fieldset
{
border: 1px solid #006c90;
width: 325px;
background: #e0997e;
}

.form-group {
margin-top: 25px;
}

.normal {
float: left;
width: 355px;
margin-right: 10px;
margin-top: 5px;
margin-bottom: 20px;
background-color: #c6c6c6;
}

.all  {
float: left;
width: 355px;
margin-top: 5px;
margin-bottom: 20px;
background-color: #e0e0e0;
}

.all > p {
font-size: 14px;
}

.normal > p {
font-size: 14px;
}


main {
grid-row: 2;
margin-top: 0;
grid-column-start: 2;
grid-column-end: 7;
margin-top: 30px;
}

.page-header {
margin-left: 0;
}

.img-grid {
display: grid;
grid-template-columns: repeat(auto-fill, 150px);
grid-auto-rows: 150px;
grid-gap: 1px;
margin-left: 10px;
margin-bottom: 25px;
}

article {
width: 100%;
max-width: 1000px;
margin-top: 20px;
margin-left: 0;
margin-bottom: 20px;
}

.paragraph {
	max-width: 1000px;
	text-align: left;
    text-justify: none;
	margin-right: 10px;
    margin-left: 0;
  }


.footnote {
display: block;
 }

.img-gallery-paragraph {
	max-width: 1000px;
	width: 70%;
	text-align: justify;
    text-justify: inter-word;
	margin-right: 10px;
}

.prettyprint {
font-size: 14px;
width: 350px;
background: #ffffff;
float: left;
margin-right: 15px;
}

.border {
border: 1px solid black;
}

.lb-data .lb-caption {
    font-size: 14px;
    font-weight: 400;
    line-height: 14px;
}

.google-cse-container {
max-width: 600px;
padding: 0;
}

pre {
    float: left;
}